:root {
    --main-color: #212121;
  }
  
  body {
    margin: 0;
    padding: 0;
    overflow: hidden;
    font-family: sans-serif;
  }
  
  .hotspot-container {
    position: absolute;
    position: absolute;
    bottom: 14px;
    z-index: 3;
  }
  
  .hotspot {
    margin-top: -8px;
    margin-left: -15px;
    position: absolute;
    width: 30px;
  }
  
  .play-pause {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.53);
    top: 0;
    opacity: 0;
    transition: .3s;
  }
  
  .play-pause:hover {
    opacity: 1;
  }
  
  .play-pause .button {
    width: 100px;
    cursor: pointer;
    position: absolute;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    left: 50%;
  }
  
  .play-pause .button__bg {
    fill: #ffffff;
  }
  
  .play-pause .button__icon {
    fill: #000000;
  }
  
  .play-pause .hide {
    opacity: 0;
    pointer-events: none;
  }
  
  .seeker {
    position: relative;
    width: 100%;
    height: 5px;
    padding-top: 20px;
    padding-bottom: 10px;
    cursor: pointer;
    margin-top: 10px;
  }
  
  .seeker__line {
    position: absolute;
    width: inherit;
    height: inherit;
  }
  
  .seeker__knob {
    width: 15px;
    height: 15px;
    background-color: #CAEBF2;
    border-radius: 50%;
    position: absolute;
    top: 15px;
    left: 0;
    z-index: 2;
  }
  
  .seeker__line--bg {
    background-color: #cccccc;
    border-radius: 5px;
  }
  
  .seeker__line--mover {
    background-color: rgba(13, 13, 13, 0.5);
    border-radius: 5px;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: top left;
            transform-origin: top left;
  }
  
  .seeker__line--current {
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: top left;
            transform-origin: top left;
    background-color: #CAEBF2;
  }
  
  .star {
    width: 100%;
    fill: #212121;
    fill: var(--main-color);
    cursor: pointer;
    z-index: 1;
  }
  
  .timer {
    background-color: #000000;
    color: #ffffff;
    width: auto;
    padding:10px;
    font-family: "Questrial" sans-serif;
    border-radius:25px;
    position: absolute;
    top: -18px;
    font-size: 11px;
    text-align: center;
  }
  
  .timer--hide {
    opacity: 0;
  }
  
  .tool-tip {
    padding: 10px 10px 0 10px;
    background-color: #212121;
    background-color: var(--main-color);
    width: 200px;
    position: absolute;
    border-radius:10px;
    pointer-events: none;
    left: -4px;
    top: -100%;
    opacity: 0;
    color: #ffffff;
    -webkit-transform-origin: left bottom;
            transform-origin: left bottom;
    -webkit-transform: rotate(20deg) translate(-39%, -93%);
            transform: rotate(20deg) translate(-39%, -93%);
    transition: 0.6s 0.1s cubic-bezier(0.23, 1, 0.32, 1);
  }
  
  .tool-tip__image {
    background-color: aliceblue;
  }
  
  .tool-tip__description {
    padding: 10px 0;
    margin: 0;
  }
  
  .tool-tip::after {
    width: 0;
    height: 0;
    content: '';
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
    border-top: 20px solid #212121;
    border-top: 20px solid var(--main-color);
    top: 99.5%;
    position: absolute;
    left: 0;
  }
  
  .player {
    width: 80%;
    max-width: 640px;
    margin: 0 auto;
    position: relative;
  }
  
  .video-container {
    width: 100%;
    height: auto;
    position: relative;
  }
  
  .video {
    width: 100%;
  }
  
  .seeking-overlay {
    width: 100%;
    height: 100%;
    background-color: #000000;
    display: block;
    position: absolute;
    opacity: 0;
    top: 0;
    transition: .2s;
  }
  
  .seeking-overlay--show {
    opacity: 1;
  }
  .app{
      background-color: #212121;
  }
/* CSS VARIABLES */
:root {
  --primary: #141414;
  --light: #F3F3F3;
  --dark: 	#686868;
}

html, body {
  width: 100vw;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  background-color: #141414;
  background-color: var(--primary);
  color: #F3F3F3;
  color: var(--light);
  font-family: Arial, Helvetica, sans-serif;
  box-sizing: border-box;
  line-height: 1.4;
}

img {
  max-width: 100%;
}

h1 {
  padding-top: 60px;
}

.wrapper {
  margin: 0;
  padding: 0;
}

/* HEADER */
header {
  padding: 20px 20px 0 20px;
  position: fixed;
  top: 0;
  display: grid;
  grid-gap:5px;
  grid-template-columns: 1fr 4fr 1fr;
  grid-template-areas:
   "nt mn mn sb . . . ";
  background-color: #141414;
  background-color: var(--primary);
  width: 100%;
  margin-bottom: 0px;
}

.netflixLogo {
  grid-area: nt;
  object-fit: cover;
  width: 100px;
  max-height: 100%;

  padding-left: 30px;
  padding-top: 0px;
}

.netflixLogo img {
  height: 35px;
}

#logo {
  color: #E50914;
  margin: 0;
  padding: 0;
}


.main-nav {
  grid-area: mn;
  padding: 0 30px 0 20px;
}

.main-nav a {
  color: #F3F3F3;
  color: var(--light);
  text-decoration: none;
  margin: 5px;
}

.main-nav a:hover {
  color: #686868;
  color: var(--dark);
}

.sub-nav {
  grid-area: sb;
  padding: 0 40px 0 40px;
}

.sub-nav a {
  color: #F3F3F3;
  color: var(--light);
  text-decoration: none;
  margin: 5px;
}

.sub-nav a:hover {
  color: #686868;
  color: var(--dark);
}


/* MAIN CONTIANER */
.main-container {
  padding-bottom: 50px;
  padding-top: 0px;
  padding-left: 0px;
  padding-right: 0px;
}

.box {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(4, minmax(100px, 1fr));
}

.box a {
  transition: -webkit-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
}

.box a:hover {
  transition: -webkit-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

.box img {
  border-radius: 2px;
}

/* LINKS */
.link {
  padding: 50px;
}

.sub-links ul {
  list-style: none;
  padding: 0;
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(4, 1fr);
}

.sub-links a {
  color: #686868;
  color: var(--dark);
  text-decoration: none;
}

.sub-links a:hover {
  color: #686868;
  color: var(--dark);
  text-decoration: underline;
}

.logos a{
  padding: 10px;
}

.logo {
  color: #686868;
  color: var(--dark);
}


/* FOOTER */
footer {
  padding: 20px;
  text-align: center;
  color: #686868;
  color: var(--dark);
  margin: 10px;
}

/* MEDIA QUERIES */

@media(max-width: 900px) {

  header {
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(2, 1fr);
    grid-template-areas:
    "nt nt nt  .  .  . sb . . . "
    "mn mn mn mn mn mn  mn mn mn mn";
  }

  .box {
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(4, minmax(100px, 1fr));
  }

}

@media(max-width: 700px) {

  header {
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(2, 1fr);
    grid-template-areas:
    "nt nt nt  .  .  . sb . . . "
    "mn mn mn mn mn mn  mn mn mn mn";
  }

  .box {
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(3, minmax(100px, 1fr));
  }

  .sub-links ul {
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(3, 1fr);
  }

}

@media(max-width: 500px) {

  .wrapper {
    font-size: 15px;
  }

  header {
    margin: 0;
    padding: 20px 0 0 0;
    position: static;
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(1, 1fr);
    grid-template-areas:
    "nt"
    "mn"
    "sb";
    text-align: center;
  }

  .netflixLogo {
    max-width: 100%;
    margin: auto;
    padding-right: 20px;
  }

  .main-nav {
    display: grid;
    grid-gap: 0px;
    grid-template-columns: repeat(1, 1fr);
    text-align: center;
  }

  h1 {
    text-align: center;
    font-size: 18px;
  }



  .box {
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(1, 1fr);
    text-align: center;
  }

  .box a:hover {
    transition: -webkit-transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
    -webkit-transform: scale(1);
    transform: scale(1.2);
  }

  .logos {
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(2, 1fr);
    text-align: center;
  }

  .sub-links ul {
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(1, 1fr);
    text-align: center;
    font-size: 15px;
  }


}


